.about__

    &container 
        margin-top: 20rem
        display: flex
        flex-direction: row
        justify-content: space-between

    &block
        height: 50rem
        width: 47vw

    &content
        width: 100%

        border-radius: 0rem 5rem 5rem 0rem
        background-color: #CDC7BD

        padding: 3.5rem 0

        display: flex
        flex-direction: column
        align-items: flex-end

        &_right
            margin-top: 16rem
            border-radius: 5rem 0rem 0rem 5rem
            background-color: #8D322F
            align-items: flex-start

    &title 
        width: 28rem
        text-align: left
        color: #8D322F
        margin: 0
        margin-bottom: 2.5rem
        margin-right: 5rem

        font-family: Prosto One
        font-size: 2rem
        font-weight: 400

        &_right
            color: #CDC7BD

            margin-left: 6rem
            margin-right: 0

    &desc
        color: #000
        font-family: Montserrat
        margin-right: 5rem

        width: 28rem
        font-size: 1.2rem

        &_right
            color: #FFF
            
            margin-left: 6rem
            margin-right: 0