.slider__

    &container 
        margin-top: 20rem

    &title 
        color: #8D322F
        font-family: Prosto One
        font-size: 2rem
        font-weight: 400
        margin-bottom: 4rem
        margin-left: 10rem

    &block 
        width: 100%
        display: flex
        align-items: center
        margin-left: 1rem

    &item
        width: 23rem !important  
        
        &__img
            width: 23rem
            height: 23rem
    
    &name
        color: #7B7B7B

        font-size: 1.25rem
        font-weight: 500
        margin: 1rem 0 0.5rem 0

    &price 
        color: #7b7b7b

        font-size: 1.25rem
        font-weight: 500