.articles__
    &container 
        display: flex
        flex-direction: row
        justify-content: space-between
        padding: 28rem 10rem 0

        font-family: Montserrat

    &block
        width: 14.5rem
        height: 27rem
        margin-top: 5rem
        padding: 0 1.5rem
        display: flex
        align-items: center
        flex-direction: column
        
        background-color: #8D322F

        &_white
            background-color: #CDC7BD

            .articles__block__desc
                color: #000

        &__desc
            display: flex
            align-items: center
            height: 15rem

            position: relative
            top: -5rem
            color: #FFF
            text-align: center
            font-size: 0.9rem
        
    &img
        width: 14rem
        height: 14rem

        position: relative
        top: -5rem