header 
    display: flex
    flex-direction: column
    padding: 0 10rem

    font-family: Montserrat
    
.header__

    &navbar
        padding: 2rem 0
        
        display: flex
        flex-direction: row
        justify-content: space-between
        align-items: center

    &navlist
        list-style-type: none
        
        display: flex
        flex-direction: row
        align-items: center

        &__item
            font-weight: 400
            margin-right: 2rem
            width: 5.8rem

            cursor: pointer

            &:hover
                font-weight: 600

            &:last-child
                margin-right: 0

            &_active
                font-weight: 600

    &city-block

        &__line
            border: 0
            height: 0.03rem
            width: 4.5rem
            background-color: #000
            margin: 0.4rem auto 0
    
    &button
        height: 1.5rem
        width: 1.5rem
        padding: 0
        object-fit: cover

        cursor: pointer

        &:hover
            transform: scale(1.1)

    &btn-block
        width: 7.5rem

        display: flex
        justify-content: space-between
        flex-direction: row

    &bg-ellipse
        position: absolute
        z-index: -1
        right: 0
        top: 0

    &bg-word
        position: absolute
        top: 8.25rem
        right: 0

        width: 25rem
        font-size: 2rem

        color: #780000
        font-family: Julius Sans One

        &_marked
            color: #FFF

    &bg-image
        position: absolute
        top: 12.5rem
        right: 16.5rem

    &info-block

        margin-top: 14rem
        width: 81vw

        &__title
            font-size: 2rem
            font-style: normal
            font-weight: 500
            width: 33rem

        &__desc
            color: #7B7B7B
            font-size: 1.25rem
            width: 36rem

        &__btn
            margin-top: 5rem

            border: 2px solid #8D322F
            background: 0
            padding: 0.6rem 1rem

            color: #8D322F
            font-family: Montserrat
            font-size: 1.25rem
            font-weight: 500

            transition: .5s

            &:hover
                color: #FFF
                background-color: #8D322F