.feedback__

    &container
        display: flex
        flex-direction: row
        justify-content: space-between

        margin-top: 12rem

    &bg 
        position: relative

    &bg__entity
        width: 36vw
        height: 40vw
        border-radius: 0rem 14rem 14rem 0rem
        background: #8D322F

        position: absolute
        top: 10vw

    &bg__image
        z-index: 100
        top: 0rem
        left: 12rem
        position: absolute

        width: 30vw
        height: 43vw

    &form-block
        display: flex
        align-items: center
        height: 51vw
    
    &form
        width: 45vw
        background-color: #CDC7BD

        padding: 3rem 0 3rem 6vw

    &form__title
        color: #8D322F

        font-family: Prosto One
        font-size: 2rem
        font-weight: 400

        margin: 0

    &form__field
        font-size: 1rem
        margin-top: 2rem
        color: #000A

        width: 23rem
        padding: 0.75rem 0
        background: 0
        border: 0
        border-bottom: 2px solid #8D322F
        display: block

        &:focus
            outline: 0

    &form__btn
        color: #FFF
        border: 2px solid #FFF0

        font-family: Montserrat
        font-size: 1.25rem

        margin-top: 5rem
        padding: 1rem 2rem
        background: #8D322F

        transition: .5s

        &:hover
            background-color: #fff0
            color: #8D322F
            border: 2px solid #8D322F