footer
    margin-top: 12rem
    display: flex
    padding: 4rem 10rem 10rem
    flex-direction: row
    background-color: #8D322F
    justify-content: space-between

    color: #FFF
    font-family: Montserrat

.footer__

    &logo
        width: 10rem
        height: 2.5rem

    &social
        margin-top: 4.5rem
        width: 10rem
        display: flex
        flex-direction: row
        justify-content: space-around
        transition: .5s
        cursor: pointer

        &__item:hover
            transform: scale(1.1)


    &title 
        margin: 0
        margin-bottom: 1.75rem

    &list 
        list-style-type: none
        padding: 0
    
        &__item
            font-size: 1.25rem
            margin-bottom: 0.45rem
            padding-bottom: 0.25rem
            transition: .5s
            text-decoration: underline #FFF0
            cursor: pointer

            &:hover
                text-decoration: underline #FFF

    &phone 
        font-size: 1.25rem

        &-caption
            color: #FFF7

            font-size: 1rem

    &btn-submit
        margin-top: 2.5rem
        color: #8D322F
        border: 2px solid #FFF

        font-family: Montserrat
        font-size: 1.25rem

        padding: 0.75rem 1.5rem

        border-radius: 1.8rem
        background-color: #FFF

        transition: .5s

        &:hover
            background-color: #fff0
            color: #fff